@import url(https://fonts.googleapis.com/css?family=Seymour+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  height: 100vh
}


.nav-bar .logo-text{
  font-family: 'Seymour One', sans-serif;
  font-size: 25px;
  width: 200px;
}

.avatar{
  border-radius: 50%;
  width: 50px;
  height:50px;
}
.nav-bar .logo{
  width: 50px;
}
.tasks-menu h1{
  margin: 0;
}

.notification{
  font-size: 25px;
  -webkit-transform: rotate(-30deg);
          transform: rotate(-30deg);
  position: relative;
  height: 25px;
  width: 25px;
}
.nav-bar .notification-badge{
  font-size: 14px;
  width: 20px;
  text-align: center;
  border-radius: 50%;
  background-color: red;
  position: absolute;
  top:15px
}
.wrapper{
  height:100vh;
}

li{
  list-style: none;
}

.nav-bar{
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
}

.top-left{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 250px;
  width:350px;
}

.top-right{
  position: relative;
}

.user-menu{
  background-color: rgb(236, 236, 236);
  text-align: center;
  width: 80px;
  position: absolute;
  right: 5px;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  z-index: 2;
}

.user-menu li{
  box-sizing: border-box;
  margin: 3px;
  cursor: pointer;
}

.main-container{
  height: calc(100%-60px);
  /* background-color: #F3F3F3; */
  padding: 15px;
}

.progress-bar{
  width: 90%;
  height: 10px;
  border-radius: 4px;
  background-color: lightgray;
}

.progress-bar-inner{
  /* width: 80%; */
  height:10px;
  border-radius: 4px;
  background-color: red;
  
}

td{
  text-align: center;
  min-width: -webkit-max-content;
  min-width: -moz-max-content;
  min-width: max-content;
}



.Medium{
  color:white;
  background-color: #FFA120;
}

.High{
  color:white;
  background-color: #FF0043
}

.Low{
  color:white;
  background-color: #00CB6F;
}

tbody{
  background-color: rgb(235, 235, 235);
}

.week-header{
  color: #B37FCD;
  font-size: 20px;
}

.purple{
  background-color: #B37FCD;
}

.timeline{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 52px;
}

.log-in-form , .add-task-form{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 400px;
  width: 50vw;
  min-width: 450px;
  background-color: white;
  border-radius: 10px;
  border: 2px solid #B37FCD;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  top: 10%;
}

.login-form{
  display: flex;
  flex:1 1;
  flex-direction: column;
  /* height: 40%; */
  justify-content: space-around;
  font-size: 16px;
}
.login-form a{
  color:gray;
  font-size: 15px;
}
.login-error p{
  color:red;
  font-size: 14px;
}

.login-button{
  background-color: #B37FCD;
  border:0;
  height: 25px;
  border-radius: 10px;
  color:white;
  font-size: 16px;
}
.login-logo{
  /* width:50%; */
  flex:1 1;
}

a{
  text-decoration: none;
}
.login-container{
  display: flex;
  width:80%;
}

.login-error{
  height: 60px;
}

.icons{
  position: absolute;
  font-size: 25px;
  right: 15px;
  color: #B37FCD;
}
.icons svg{
  cursor: pointer;
  margin: 5px;
}

.tasks-wrapper{
  display: flex;
  flex-direction: column;
  background-color: lightgray;
}

.task-box{
  background-color: #B37FCD;
  color:white;
  font-size: 20px;
  /* width: 20%; */
  flex:1 1;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

.grid-row{
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin:15px;
}

.grid-name{
  width:200px;
  text-align: center;
  font-size: 20px;
}

.profile-key{
  font-weight: bold;
  float: left;
  width: 150px;
}
.nav-bar .notification-menu{
  position: absolute;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  background-color: rgb(236, 236, 236);
  width: 120px;
}
table {
    border-collapse: collapse;
    margin: 20px 0;
  }
  tr {
    background: #eee;
    border-bottom: 1px solid #fff;
  }
  tr, td {
    padding: 0;
  }
  tr td div {
    max-height: 0;
    /* padding: 0 10px; */
    box-sizing: border-box;
    overflow: hidden;
    -webkit-transition: max-height 0.3s, padding 0.3s;
    transition: max-height 0.3s, padding 0.3s;
  }
  tr.active td div {
    max-height: 100px;
    /* padding: 10px 10px; */
    -webkit-transition: max-height 0.6s, padding 0.6s;
    transition: max-height 0.6s, padding 0.6s;
  }
